<template>
    <div class="bg-white bg-shadow rounded-lg m-5 p-3">
        <p class="p-4">PENGUMUMAN</p>
        <hr class="style16">
        <div class="shadow-lg rounded-lg p-3 mt-2" v-for="(item, index) in pengumuman" :key="index">
            <p v-show="item.file">
                <a class="btn btn-primary rounded-lg float-right" :href="`https://infolayanans.space/${item.file}`">Download file</a>
            </p>
            <p class="p-2 font-bold font-courier">
                {{item.judul}}
            </p>
            <p class="p-2">{{item.keterangan}}</p>
            <hr class="style14">
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default{
    layout:'awal',
    data(){
        return{
            pengumuman:[]
        }
    },
    mounted() {
        let fd = new FormData()
        fd.append('data', `select * from sekolah_pengumuman`)//database setting di server
        fd.append('database','infolay3_test')
        axios.post('https://infolayanans.space/api/mysql/auto.php', fd
        , {
            headers: {
            Authorization: localStorage.getItem('auth._token.local')
            }
        }).then(res => {
            this.pengumuman=res.data
            console.log(res)
            this.$forceUpdate()
        })
    },
}
</script>